import React, { Component } from 'react'
import styled from 'styled-components'

const Container = styled.div`
  width: 100%;
  flex-wrap: wrap;
  display: flex;
  align-items: center;
  margin: 20px;
  box-sizing: border-box;
`
const ImageContainer = styled.div`
  width: 100px;
  height: 100px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  margin: auto;
  /* margin-right: 20px; */
`
const TextContainer = styled.p`
  margin-bottom: 0px;
  width: 100%;
  text-align: center;
  color: #bdbdbd;
  margin-top: 4px;
  margin-bottom: 15px;
  font-size: 12px;
`

const options = {
  git: [
    {
      link: 'https://gitlab.com/ttoonoott',
      image: 'data:image/png;base64,',
      name: 'Gitlab',
    },
    {
      link: 'https://github.com/toonPt0473',
      image: 'data:image/png;base64,',
      name: 'Github',
    },
  ],
  framework: [
    {
      link: 'https://expressjs.com/',
      image: 'data:image/png;base64,',
      name: 'ExpressJs',
    },
    {
      link: 'https://loopback.io/',
      image: 'https://cdn-images-1.medium.com/max/300/0*mzZmHlu6vXwC5G6N.png',
      name: 'Loopback',
    },
    {
      link: 'https://redux.js.org/',
      image: 'https://cdn-images-1.medium.com/max/312/1*SRL22ADht1NU4LXUeU4YVg.png',
      name: 'Redux',
    },
    {
      link: 'https://github.com/redux-saga/redux-saga',
      image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQTRuDKhf9Ec-iDXXFK5wJIEm0KSq4Oa2bQRw&usqp=CAU',
      name: 'Redux saga',
    },
    {
      link: 'https://jquery.com/',
      image: 'https://cdn-images-1.medium.com/max/1600/0*g3ns8QALNBBH7CBA.',
      name: 'Jquery',
    },
    {
      link: 'https://ant.design/',
      image: 'https://gw.alipayobjects.com/zos/rmsportal/KDpgvguMpGfqaHPjicRK.svg',
      name: 'Antd',
    },
    {
      link: 'https://tailwindcss.com/',
      image: 'data:image/png;base64,',
      name: 'Tailwind',
    },
    {
      link: 'https://getbootstrap.com/',
      image: 'data:image/png;base64,',
      name: 'Bootstrap',
    },
    {
      link: 'https://bulma.io/',
      image: 'https://bulma.io/images/bulma-logo.png',
      name: 'Bulma',
    },
    {
      link: 'https://docs.docker.com/compose/',
      image: 'https://cdn-images-1.medium.com/max/453/0*lQHBTNViWBhPsTtF.',
      name: 'Docker-compose',
    },
    {
      link: 'https://graphql.org/',
      image: 'https://upload.wikimedia.org/wikipedia/commons/thumb/1/17/GraphQL_Logo.svg/1024px-GraphQL_Logo.svg.png',
      name: 'Graphql',
    },
  ],
}

export class Git extends Component { // eslint-disable-line
  renderGrid = (type) => options[type].map((item, index) => (
    <a href={item.link} target="_blank" style={{ marginRight: 20, textDecoration: 'none' }} key={index.toString()}>
      <ImageContainer style={{ backgroundImage: `url(${item.image})` }} />
      <TextContainer>{item.name}</TextContainer>
    </a>
  ))
  render() {
    return (
      <Container>
        {this.renderGrid(this.props.type)}
      </Container>
    )
  }
}

export default Git



